import { useConcreteFormHandler } from '../context/concreteForm.context';
export const useFormState = () => useConcreteFormHandler().getFormState();
export const useControlState = (name) => useConcreteFormHandler().getControlState(name);
export const useControlProps = (name, options) => useConcreteFormHandler().getControlProps(name, options);
export const useControlActions = (name) => {
    const handler = useConcreteFormHandler();
    return {
        setFieldValue: (name, value, shouldValidate, shouldTouch) => handler.setFieldValue(name, value, shouldValidate, shouldTouch),
    };
};
